#screen-menu {

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;

  h1 {
    color: white;
    font-family: "connections", sans-serif;
    font-size: 5em;
    cursor: default;
  }

  label {
    color: white;
    font-family: "retro", sans-serif;
    cursor: default;
  }

  button {
    width: 256px;
    height: 48px;
    color: white;
    font-family: "retro", sans-serif;
    border: 4px solid white;
    background: black;
    cursor: pointer;
    margin-top: 12px;
  }

  button:hover {
    font-size: 1em;
  }
}
